.App {
  margin: 30px auto;
  padding: 10px;
  width: 50vw;
  height: auto;
  border: 2px solid teal;
  border-radius: 8px;
  text-align: center;
  background: linear-gradient(0deg, rgba(34,193,195,1) 0%, rgba(253,187,45,1) 100%);
  word-wrap: break-word;
}

.app-title {
  text-shadow: 2px 2px 2px pink;
}

.todo-input {
  padding: 10px;
  font-size: 16px;
  margin: 10px;
  border-radius: 8px;
  height: 20px;
  color: #060885;
  border: 2px solid teal;

}

.todo-input:focus {
  outline: 4px dashed lightskyblue;
      outline-offset: 0.2em;
}

.button {
  padding: 10px 16px;
  font-size: 16px;
  background-color: #dbbb53;
  border-radius: 8px;
  cursor: pointer;

}

.button:hover {
  background-color: #a99221;
  color: #ebf7eb;
}


.list {
  margin: 5px 40px;
}

.list-item {
  padding: 4px;
  margin: 2px;
  font-size: 18px;
  color:#060885;
  border: 1px solid gray;
  border-radius: 20px 20px 20px 20px;
  box-shadow: inset 0px 8px 10px 0px rgba(256,235,235,1);
  list-style-type: disclosure-closed; 
}

.text-counter{
  font-size: 18px;
  font-weight: bold;
  color: teal;
  text-shadow: 1px 1px #c0d806;
}
